<template>
	<view class="body">
		<view class="head">
			<view class="title">恭喜你</view>
			<view class="text">已完成本次估分</view>
			<view class="num">总分:<view>{{total}}</view></view>
			<view class="review" @click="backAssessment">点击重新估分</view>
		</view>
		<view class="info">
			<image class="report" src="../../static/assessment/report_icon.png" mode="widthFix"></image>
			<view class="title">考研信息</view>
			<view class="cell">
				<view class="label">省市：</view>
				<input type="text" disabled v-model="data.area_name">
			</view>
			<view class="cell">
				<view class="label">招生单位：</view>
				<input type="text" disabled v-model="data.school_name">
			</view>
			<view class="cell">
				<view class="label">专业名称：</view>
				<input type="text" disabled v-model="data.three_major_name">
			</view>
		</view>
		<view class="achievement">
			<view class="title">单科成绩</view>
			<view class="list">
				<view class="one">
					<view class="cell" v-if="data.one_subject_title!='无'">{{data.one_subject_title}}</view>
					<view class="cell" v-if="data.two_subject_title!='无'">{{data.two_subject_title}}</view>
					<view class="cell" v-if="data.three_subject_title!='无'">{{data.three_subject_title}}</view>
					<view class="cell" v-if="data.four_subject_title!='无'">{{data.four_subject_title}}</view>
				</view>
				<view class="two">
					<view class="cell" v-if="data.one_subject_title!='无'">{{data.one_subject_score || '0'}}</view>
					<view class="cell" v-if="data.two_subject_title!='无'">{{data.two_subject_score || '0'}}</view>
					<view class="cell" v-if="data.three_subject_title!='无'">{{data.three_subject_score || '0'}}</view>
					<view class="cell" v-if="data.four_subject_title!='无'">{{data.four_subject_score || '0'}}</view>
				</view>
				<view class="three">
					<view class="cell" @click="toAssessment(data.one_subject_attr,data.one_subject_id)" v-if="data.one_subject_title!='无'">查看题目</view>
					<view class="cell" @click="toAssessment(data.two_subject_attr,data.two_subject_id)" v-if="data.two_subject_title!='无'">查看题目</view>
					<view class="cell" @click="toAssessment(data.three_subject_attr,data.three_subject_id)" v-if="data.three_subject_title!='无'">查看题目</view>
					<view class="cell" @click="toAssessment(data.four_subject_attr,data.four_subject_id)" v-if="data.four_subject_title!='无'">查看题目</view>
				</view>
			</view>
		</view>
		<view class="module" v-if="show">
			<view class="models">
				<view class="title">确认重新估分吗？</view>
				<view class="buttons">
					<view class="cancel" @click="show=false">取消</view>
					<view class="sure" @click="sureResubject">确定</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		assessment ,reSubject
	} from '@/common/http.api.js';
	export default {
		data() {
			return {
				token:'',
				total:'',
				show:false,
				data:{}
			}
		},
		onLoad() {
			this.token = store.state.vuex_token
			assessment({token:this.token}).then(res=>{
				console.log(res)
				this.data = res.data.data
				this.total = res.data.data.one_subject_score + res.data.data.two_subject_score + res.data.data.three_subject_score + res.data.data.four_subject_score
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 显示确认框
			backAssessment() {
				this.show = true 
			},
			// 重新估分
			sureResubject() {
				reSubject({token:this.token}).then(res=>{
					console.log(res)
					this.$u.toast(res.data.data+',即将返回')
					setTimeout(()=>{
						this.show = false 
						uni.switchTab({
							url:'/pages/assessment/assessment'
						})
					},1500)
				}).catch(err=>{
					console.log(err)
				})
			},
			// 去题目
			toAssessment(state,id) {
				if(state==1){
					uni.navigateTo({
						url:'/pages/assessment/answer?subject_id='+id
					})
				}else{
					this.$u.toast('该科属于自主估分')
				}
			}
		}
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 1vh 0;
		min-height: 98vh;
		background-color: #ffc845;
		
		.module{
			width: 100vw;
			height: 100vh;
			position: fixed;
			top: 0;
			right: 0;
			z-index: 999;
			background: rgba(0, 0, 0, .3);
			display: flex;
			justify-content: center;
			align-items: center;
			.models{
				background-color: #fff;
				border-radius: 20rpx;
				width: 56%;
				.title{
					font-size: 32rpx;
					font-weight: 500;
					text-align: center;
					padding: 40rpx 0;
					margin-bottom: 20rpx;
				}
				.buttons{
					width: 100%;
					display: flex;
					justify-content: space-between;
					align-items: center;
					view{
						line-height: 78rpx;
						font-size: 30rpx;
						width: 50%;
						text-align: center;
					}
					.cancel{
						background-color: #eee;
						border-bottom-left-radius: 20rpx;
						border: 1rpx solid #eee;
					}
					.sure{
						background-color: #ffd656;
						color: #fff;
						border-bottom-right-radius: 20rpx;
						border: 1rpx solid #ffd656;
					}
				}
			}
		}
		.head{
			width: 50%;
			margin-left: 2%;
			text-align: center;
			color: #fff;
			.title{
				font-size: 58rpx;
				letter-spacing: 4rpx;
			}
			.text{
				font-size: 38rpx;
				letter-spacing: 2rpx;
				margin: 20rpx 0;
			}
			.num{
				color: #ffc845;
				font-size: 34rpx;
				background-color: #fff;
				width: 75%;
				margin: 0 auto;
				border-radius: 50rpx;
				line-height: 78rpx;
				text-align: center;
				view{
					font-size: 44rpx;
					display: inline-block;
				}
			}
			.review{
				width: 58%;
				margin: 30rpx auto;
				font-size: 34rpx;
				padding-bottom: 10rpx;
				border-bottom: 1rpx solid #fff;
			}
		}
		.info{
			background-color: #fff;
			border-radius: 20rpx;
			padding: 30rpx;
			margin: 0 5%;
			position: relative;
			margin-bottom: 20rpx;
			.report{
				width: 300rpx;
				position: absolute;
				bottom: 100%;
				right: 0;
			}
			.title{
				font-size: 38rpx;
				margin-bottom: 40rpx;
			}
			.cell{
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 10rpx;
				.label{
					color: #545454;
					font-size: 30rpx;
				}
				input{
					border: 1rpx solid #eee;
					padding: 0 10rpx;
					font-size: 30rpx;
					width: 60%;
					height: 68rpx;
					line-height: 68rpx;
				}
			}
		}
		.achievement{
			background-color: #fff;
			border-radius: 20rpx;
			padding: 30rpx;
			margin: 0 5%;
			.title{
				font-size: 38rpx;
				margin-bottom: 40rpx;
			}
			.list{
				text-align: center;
				font-size: 32rpx;
				line-height: 68rpx;
				.one{
					display: flex;
					.cell{
						width: 24%;
						font-weight: 500;
						border: 1rpx solid #ffc845;
						margin:0px -1px -1px 0px ;
						background-color: #ffebbb;
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
					}
				}
				.two{
					display: flex;
					.cell{
						width: 24%;
						border: 1rpx solid #ffc845;
						margin:0px -1px -1px 0px ;
					}
				}
				.three{
					display: flex;
					.cell{
						width: 24%;
						font-size: 28rpx;
						border: 1rpx solid #ffc845;
						margin:0px -1px 0px 0px ;
					}
				}
			}
		}
	}
</style>